
import { Options, Vue, setup } from 'vue-class-component';
import { useRoute } from 'vue-router';

import { useConsultationInfo, usePagedData } from '@/composable';

import { DoctorCommonDto } from '@/types/models';

import DoctorAppointmentServiceInstance from '@/services/doctors';

import { AsyncCb } from '@/store/stores/pagedData/types';

import { getSpecialityFromBrowserStorage } from '@/utils';

import {
  RCard,
  RCardActions,
  RCardSection,
} from '@/ui/molecules';
import { RImg, RSeparator, RSpinner } from '@/ui/atoms';
import { DefaultCardsRows } from '@/ui/organisms';

@Options({
  components: {
    RCard,
    RCardActions,
    RCardSection,
    RImg,
    RSeparator,
    DefaultCardsRows,
    RSpinner,
  },
})
export default class DoctorsBySpecs extends Vue {
  doctorsBySpecs = setup(() => {
    const route = useRoute();

    function asyncCbWrapper(page: number, count: number): AsyncCb<DoctorCommonDto> {
      const { getDoctors } = DoctorAppointmentServiceInstance;

      return (): ReturnType<typeof getDoctors> => getDoctors({
        params: {
          SpecialityId: route.params.specialityId as string,
          Page: page,
          Count: count,
        },
      });
    }

    return usePagedData<DoctorCommonDto>('doctorsBySpecs', asyncCbWrapper);
  });

  consultationInfo = setup(useConsultationInfo);

  openDoctorAppointment(doctorCommon: DoctorCommonDto): void {
    const { saveDoctorToStoreAndBrowserStorage } = this.consultationInfo.consultationInfoStore;
    const { id } = doctorCommon;

    saveDoctorToStoreAndBrowserStorage(doctorCommon);

    this.$router.push({
      name: this.$routeNames.DOCTOR_APPOINTMENT,
      params: {
        doctorId: id,
      },
    });
  }

  mounted(): void {
    this.$analyticsUtils.pushEvent('speciality_open', {
      eventLabel: getSpecialityFromBrowserStorage()?.specialityName ?? '',
      eventCategory: 'telemed',
    });
  }
}
